<template>
   <div class='categories'>
       <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/sy' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
             <!--  -->
            <el-row :gutter="20">
                <el-col :span="6">
                     <el-button type="primary" @click="tjfl"></el-button>
                </el-col>
            </el-row>
            <!--  -->
            <tree-table
            class="tree-table1"
             :data="list"
             :columns="columns"
             :selection-type="false"
             :expand-type="false"
             :show-index="true"
             index-text="#"
             :border="true"
             :show-row-hover="true"
            >
                <template slot="isok" slot-scope="scope">
                        <i class="el-icon-success"  style="color:lightgreen;fontSize:18px" v-if="!scope.row.cat_deleted"></i>
                         <i class="el-icon-success"  style="color:#ccc;fontSize:18px" v-else></i>
                </template>
                 <template slot="paixv" slot-scope="scope">
                        <el-tag v-if="scope.row.cat_level == 0">{{ scope.row.cat_level | gl}}</el-tag>
                        <el-tag  v-if="scope.row.cat_level == 1" type="success">{{ scope.row.cat_level | gl}}</el-tag>
                        <el-tag  v-if="scope.row.cat_level == 2" type="warning">{{ scope.row.cat_level | gl}}</el-tag>
                </template>
                 <template slot="cz" slot-scope="scope">
                             <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                            <el-button type="primary" @click="xiugai(scope.row)" size="mini" icon="el-icon-edit" ></el-button>
                            </el-tooltip>
                             <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                            <el-button type="danger" @click="shanchu(scope.row)" size="mini" icon="el-icon-delete" ></el-button>
                            </el-tooltip>
                </template> 
            </tree-table>
             <!--  -->
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[1, 2, 5, 10]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
            </el-pagination>
        </el-card>
         <!--  -->
        <el-dialog
            title=""
            :visible.sync="dialogVisible"
            @close="adddialog"
            width="60%">
            <!--  -->
            <el-form :model="xiugaiFrom" ref="addrefs" label-width="70px">
                <el-form-item  label="" prop="cat_name">
                    <el-input v-model="xiugaiFrom.cat_name"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible=false"> </el-button>
                <el-button type="primary"  @click="xiugaiqd"> </el-button>
            </span>
        </el-dialog>
         <!--  -->
        <el-dialog
            title=""
            :visible.sync="dialogVisible1"
             @close="adddialog1"
            width="60%">
            <!--  -->
            <el-form :model="addFrom" :rules="addRules"  ref="addref" label-width="100px">
                 <el-form-item  label=":" prop="cat_name">
                    <el-input v-model="addFrom.cat_name"></el-input>
                </el-form-item>
                <el-form-item label=":" >
                    <el-cascader
                    v-model="flcat_pid"
                    :options="list2"
                    :props="cascaderProps"
                    expand-trigger= "hover"
                    :clearable="true"
                     :change-on-select="true"
                     @change="flhandleChange"
                    ></el-cascader>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible1=false"> </el-button>
                <el-button type="primary" @click="fltjqd"> </el-button>
            </span>
        </el-dialog>
   </div>
</template>

<script>
export default {
   data() {
   //
       return {
           dialogVisible:false,
           dialogVisible1:false,
            //
            xiugaiFrom:{
              
            },
            flcat_pid:[],
             //
             addFrom:{
                 cat_pid:0,
                cat_name:"",
                cat_level:0
             },
           list:[],
           list2:[],
           queryInfo:{
               query:"",
               //
               pagenum:1,
               //
               pagesize:7
           },
            total:0,
            columns:[
                {
                    label: '',
                    prop:"cat_name",
                },
                {
                    label: '',
                    //
                    type: 'template',
                    //
                    template: 'isok',
                    width: '140px'
                },
                {
                    label: '',
                    //
                    type: 'template',
                    //
                    template: 'paixv',
                },
                {
                    label: '',
                    //
                    type: 'template',
                    //
                    template: 'cz',
                },
            ],
             //
             addRules:{
                 //
                  cat_name:[
                        { required: true, message: '', trigger: 'blur' },
                        { min: 1, max: 5, message: ' 1  5 ', trigger: 'blur' }
                    ],
             },
             //
              cascaderProps:{
                value:"cat_id",
                label:"cat_name",
                children:"children"
            }

       };
   },
   //import
   components: {

   },
   filters:{
      gl(val){
         if(val == "0"){
            return ""
         }else if(val == "1"){
             return ""
         }else if(val == "2"){
             return ""
         }
      }
   },
   // data
   computed: {

   },
   //data
   watch: {

   },
   //
   methods: {
       //
       async getcatlist(){
           let { data:res} = await this.$http.get(`categories`,{params:this.queryInfo})
            if(res.meta.status !== 200){
                return this.$message.error("")
            }
           this.list = res.data.result
             this.total = res.data.total
           console.log(res)
       },
       //
        async getfjfllist(){
            let { data:res} = await this.$http.get(`categories`,{params:
            {type:2}})
            if(res.meta.status !== 200){
                return this.$message.error("")
            }
            this.list2 = res.data
            console.log(this.list2)
        },
       //
       //pagesize
       handleSizeChange(newsize){
           console.log(newsize)
           //
           this.queryInfo.pagesize = newsize
         this.getcatlist()
       },
       // 
       handleCurrentChange(newpage){
           console.log(newpage)
        //    
           this.queryInfo.pagenum = newpage
            this.getcatlist()
       },
       //
       adddialog(){

           this.$refs.addrefs.resetFields()
       },
       //
       adddialog1(){
           this.$refs.addref.resetFields()
           this.flcat_pid = []
           this.addFrom.cat_level = 0
       },
        //
        async xiugai(v){
                this.dialogVisible = true
                let {data:res} = await this.$http.get(`categories/${ v.cat_id }`)
                // console.log(res)
                this.xiugaiFrom = res.data
                console.log(this.xiugaiFrom)
            },
             //
        xiugaiqd(){
            this.$refs.addrefs.validate(async valid =>{
                // console.log(valid)
                if (!valid) return
                //
              let { data:res } = await this.$http.put(`categories/${this.xiugaiFrom.cat_id}`,this.xiugaiFrom)
                    // console.log(res) 
                    if(res.meta.status == 200){
                        //  
                        this.$message({
                            message: res.meta.msg,
                            type: 'success'
                            });   
                    }else{
                        //
                        this.$message({
                            message: res.meta.msg,
                            type: 'warning'
                            });
                    }
                    this.dialogVisible = false
                    //
                   this.getcatlist()
            })
        },
         //
       async shanchu(v) {
            console.log(v)
        let conshanchu= await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err =>{
            return err
        })
        //confirm
        if(conshanchu !== "confirm"){
            return this.$message.info("")
        }
        //
        let { data:res } = await this.$http.delete('categories/'+v.cat_id)
        console.log(res)
        if(res.meta.status == 200){
            //  
            this.$message({
                message: res.meta.msg,
                type: 'success'
                });   
        }else{
            //
            this.$message({
                message: res.meta.msg,
                type: 'warning'
                });
        }
        //
       this.getcatlist()
      },
        //
        tjfl(){
            //
              this.getfjfllist()
                 this.dialogVisible1 = true
        },
        //id
        flhandleChange(){
            console.log(this.flcat_pid)
            if(this.flcat_pid.length >0){
                this.addFrom.cat_pid=this.flcat_pid[this.flcat_pid.length-1]
               this.addFrom.cat_level = this.flcat_pid.length
               return
            }else{
                 this.addFrom.cat_pid=0
               this.addFrom.cat_level = 0
            }

        },
        //
        fltjqd(){
            this.$refs.addref.validate(async valid =>{
                // console.log(valid)
                if (!valid) return
                //
              let { data:res } = await this.$http.post(`categories`,this.addFrom)
                    console.log(res) 
                    if(res.meta.status == 201){
                        //  
                        this.$message({
                            message: res.meta.msg,
                            type: 'success'
                            });   
                    }else{
                        //
                        this.$message({
                            message: res.meta.msg,
                            type: 'warning'
                            });
                    }
                    this.getcatlist()
                    this.dialogVisible1 = false
                     
            })
        }
       
   },
   // - this
   created() {
       this.getcatlist()
     
   },
   // - DOM
   mounted() {

   },
   beforeCreate() {}, // - 
   beforeMount() {}, // - 
   beforeUpdate() {}, // - 
   updated() {}, // - 
   beforeDestroy() {}, // - 
   destroyed() {}, // - 
   activated() {}, //keep-alive
}
</script>
<style lang="less" scoped>
.el-card{
    width: 100%;
    margin-top: 20px;
}
.tree-table1{
    margin-top: 20px;
}
.el-pagination{
    margin-top: 20px;
}
.el-form-item__label{
 font-size: 12px;
}
.el-cascader{
    width: 100%;
}
</style>