<template>
   <div class='reports'>
       <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/sy' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <!-- EChartsDom -->
            <div id="main" style="width: 750px;height:400px;"></div>
        </el-card>
   </div>
</template>

<script>
import echarts from 'echarts'
import _ from 'lodash'
export default {
   data() {
   //
       return {
           //
           options: {
                title: {
                    text: ''
                },
                tooltip: {
                    trigger: 'axis',
                    axisPointer: {
                    type: 'cross',
                    label: {
                        backgroundColor: '#E9EEF3'
                    }
                    }
                },
                grid: {
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                xAxis: [
                    {
                    boundaryGap: false
                    }
                ],
                yAxis: [
                    {
                    type: 'value'
                    }
                ]
                }
       };
   },
   //import
   components: {

   },
   // data
   computed: {

   },
   //data
   watch: {

   },
   //
   methods: {

   },
   // - this
   created() {

   },
   // - DOM
  async mounted() {
       // domecharts
        var myChart = echarts.init(document.getElementById('main'));
      let { data:res }= await this.$http.get(`reports/type/1`)
       if(res.meta.status !== 200){
           return this.$message.error("")
       }
        //
       let list =  _.merge(res.data,this.options)
        // 
        myChart.setOption(list)

   },
   beforeCreate() {}, // - 
   beforeMount() {}, // - 
   beforeUpdate() {}, // - 
   updated() {}, // - 
   beforeDestroy() {}, // - 
   destroyed() {}, // - 
   activated() {}, //keep-alive
}
</script>
<style lang="less" scoped>
.el-card{
    width: 100%;
    margin-top: 20px;
}

</style>