<template>
   <div class='login'>
       <div class="lxy_denglu">
           <div class="img"><img src="../assets/logo.png" alt=""></div>
         <p>
             <i class="el-icon-user-solid"></i>
            <input type="text" v-model="username">
        </p>
         <p>
             <i class="el-icon-lock"></i>
             <input type="password" v-model="password">
        </p>
         <div class="tj">
            <el-button type="primary" @click="dl"></el-button>
            <el-button></el-button> 
         </div>   
       </div>
   </div>
</template>

<script>
export default {
   data() {
   //
       return {
           username:'',
           password:''
       };
   },
   //import
   components: {

   },
   // data
   computed: {

   },
   //data
   watch: {

   },
   //
   methods: {
      async dl(){
           let { data:res } = await this.$http.post(`login`,{
                username:this.username,
                password:this.password
           })
           //token,
           window.sessionStorage.setItem("Token", res.data.token);
           if(res.meta.status == 200){
             //  
            this.$message({
                message: res.meta.msg,
                type: 'success'
                });
             this.$router.push("/sy")   
           }else{
               //
               this.$message({
                message: res.meta.msg,
                type: 'warning'
                });
           }
           console.log(res.data)
       },

   },
   // - this
   created() {

   },
   // - DOM
   mounted() {

   },
   beforeCreate() {}, // - 
   beforeMount() {}, // - 
   beforeUpdate() {}, // - 
   updated() {}, // - 
   beforeDestroy() {}, // - 
   destroyed() {}, // - 
   activated() {}, //keep-alive
}
</script>
<style lang="less" scoped>
.login{
    width: 100%;
    height: 100%;
    background: #2b4b6b;
    display: flex;
    align-items: center;
    justify-content: center;
   .lxy_denglu{
        width: 400px;
        height: 300px;
        background: #fff;
       border-radius: 5px;
        position: relative;
       p{
           width: 100%;
           display: flex;
           justify-content: center;
           align-items: center;
           margin-bottom: 20px;
           position: relative;
           &:nth-of-type(1){
               margin-top: 114px;
           }
           i{
               position: absolute;
               top: 10px;
               left: 16px;
               z-index: 999;
               color: #999;
           }
           input{
               width: 94%;
               height: 38px;
               border: 1px #ccc solid;
               border-radius: 5px;
               text-indent: 20px;
               outline-color:green;
           }
          
       }
       .img{
           width: 130px;
           height: 130px;
           background: #E9EEEA;
           border-radius: 50%;
          position: absolute;
          top: -64px;
          left: 136px;
            box-shadow: 0px 0px 5px 10px rgb(252, 250, 250);
            overflow:hidden;
           img{
               width: 100%;
               overflow:hidden
           }
       }
       .tj{
           display: flex;
           padding-right: 10px;
           justify-content: flex-end;
       }

    }
}
 
</style>