<template>
   <div class='roles'>
       <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/sy' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card> 
            <!--  -->
            <el-row :gutter="20">
                <el-col :span="6">
                     <el-button type="primary" @click="dialogVisible=true"></el-button>
                </el-col>
            </el-row>
            <!--  -->
             <el-table
                :data="list"
                border stripe
                style="width: 100%">
                <!--  -->
                <el-table-column type="expand" width="40">
                  <template slot-scope="scope">
                      <el-row :class="['bdbottom',i1 === 0 ? 'bdtop' :'','el-col1']" v-for="(item1,i1) in scope.row.children" :key="item1.id">
                        <!--  -->
                          <el-col class="el-col1" :span="4">
                             <el-tag closable @close="scqx(scope.row,item1.id)">{{item1.authName}}</el-tag><i class="el-icon-caret-right"></i>
                          </el-col>
                          <!--  -->
                          <el-col :span="19">
                              <!-- for -->
                              <el-row :class="[i2 === 0 ? '':'bdtop','el-col1']" v-for="(item2,i2) in item1.children" :key="item2.id">
                                  <el-col :span="5">
                                      <el-tag closable @close="scqx(scope.row,item2.id)" type="success">{{item2.authName}}</el-tag><i class="el-icon-caret-right"></i>
                                  </el-col>
                                  <el-col :span="18">
                                       <el-tag closable @close="scqx(scope.row,item3.id)" v-for="(item3) in item2.children" :key="item3.id"  type="warning">{{item3.authName}}</el-tag>                                         
                                  </el-col>
                              </el-row>
                          </el-col>
                      </el-row>
                      <!-- <pre>
                          {{scope.row}}
                      </pre> -->
                  </template>
                </el-table-column>
                <el-table-column
                label="#"
                   type ="index">
                </el-table-column>
                <el-table-column
                    prop="roleName"
                    label=""
                    >
                </el-table-column>
                 <el-table-column
                    prop="roleDesc"
                    label=""
                  >
                </el-table-column>
                <el-table-column label="" width="300">
                    <template slot-scope="scope">
                             <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                            <el-button  @click="xiugai(scope.row)" type="primary" size="mini" icon="el-icon-edit" ></el-button>
                            </el-tooltip>
                             <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                            <el-button @click="shanchu(scope.row)" type="danger" size="mini" icon="el-icon-delete" ></el-button>
                            </el-tooltip>
                            <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                                <el-button @click="fenpeiqx(scope.row)" type="warning" size="mini" icon="el-icon-setting"></el-button>
                            </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <!--  -->
         <el-dialog
            title=""
            :visible.sync="dialogVisible"
            width="60%">
            <!--  -->
            <el-form :model="addFrom" ref="addref" label-width="70px">
                <el-form-item  label="" prop="roleName">
                    <el-input v-model="addFrom.roleName"></el-input>
                </el-form-item>
                <el-form-item  label="" prop="roleDesc">
                    <el-input v-model="addFrom.roleDesc"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible=false"> </el-button>
                <el-button type="primary" @click="addroles"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="dialogVisible1"
            width="60%">
            <!--  -->
            <el-form :model="xiugaiFrom" ref="addrefs" label-width="70px">
                <el-form-item  label="" prop="roleName">
                    <el-input v-model="xiugaiFrom.roleName"></el-input>
                </el-form-item>
                <el-form-item  label="" prop="roleDesc">
                    <el-input v-model="xiugaiFrom.roleDesc"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible1=false"> </el-button>
                <el-button type="primary" @click="xiugaiqd"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="dialogVisible2"
            @close="gbfp"
            width="60%">
            <!--  -->
           <el-tree :data="list1" 
           ref="treeref"
           show-checkbox 
           node-key='id' 
           default-expand-all
           :default-checked-keys ="defkeys"
           :props="treeProps" >
            </el-tree>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible2=false"> </el-button>
                <el-button type="primary" @click="qdfptk"> </el-button>
            </span>
        </el-dialog>
   </div>
</template>

<script>
export default {
   data() {
   //
       return {
           list:[],
           dialogVisible:false,
            dialogVisible1:false,
             dialogVisible2:false,
           //
            addFrom:{
                roleName:"",
                roleDesc:""
            },
            //
            xiugaiFrom:{
                 roleName:"",
                roleDesc:"",
               roleId:""
            },
            //
            list1:[],
             treeProps: {
                children: 'children',
                label:'authName'
             },
             //
             defkeys:[],
             //id
             fpid:""
       };
   },
   //import
   components: {

   },
   // data
   computed: {

   },
   //data
   watch: {

   },
   //
   methods: {
       //
     async getroleslist(){
          let {data:res} = await this.$http.get('roles')
          this.list = res.data
          console.log(this.list)
      },
      //
      async addroles(){
          let { data:res } = await this.$http.post(`roles`,this.addFrom)
        //   console.log(res)
          if(res.meta.status == 201){
               this.$message({
                message: res.meta.msg,
                type: 'success'
                });

            }else{
                this.$message({
                message:"",
                type: 'warning'
                });
            }
            //
             this.getroleslist()
             this.dialogVisible = false

      },
      //
   async scqx(role,rightId){
         let conshanchu= await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err =>{
            return err
        })
         //confirm
        if(conshanchu !== "confirm"){
            return this.$message.info("")
        }
        //
        let { data:res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
        console.log(res)
        if(res.meta.status == 200){
            //  
            this.$message({
                message: res.meta.msg,
                type: 'success'
                });   
        }else{
            //
            this.$message({
                message: res.meta.msg,
                type: 'warning'
                });
        }
        role.children = res.data
       
    },
    //
   async xiugai(v){
        console.log(v)
         this.dialogVisible1 = true
         let {data:res} = await this.$http.get(`roles/${ v.id }`)
         console.log(res)
         this.xiugaiFrom.roleName = res.data.roleName
          this.xiugaiFrom.roleDesc = res.data.roleDesc
          this.xiugaiFrom.roleId = res.data.roleId
    },
    //
    xiugaiqd(){
          this.$refs.addrefs.validate(async valid =>{
                // console.log(valid)
                if (!valid) return
                //
              let { data:res } = await this.$http.put(`roles/${this.xiugaiFrom.roleId}`,this.xiugaiFrom)
                    // console.log(res) 
                    if(res.meta.status == 200){
                        //  
                        this.$message({
                            message: "",
                            type: 'success'
                            });   
                    }else{
                        //
                        this.$message({
                            message: "",
                            type: 'warning'
                            });
                    }
                    this.dialogVisible1 = false
                    //
                   this.getroleslist()
            })
    },
    //
        async shanchu(v){
            console.log(v)
                 let conshanchu= await this.$confirm(', ?', '', {
                  confirmButtonText: '',
                  cancelButtonText: '',
                  type: 'warning'
                }).catch(err =>{
                    return err
                })
                 //confirm
                if(conshanchu !== "confirm"){
                    return this.$message.info("")
                }
                //
                let { data:res } = await this.$http.delete(`roles/${v.id}`)
                console.log(res)
                if(res.meta.status == 200){
                    //  
                    this.$message({
                        message: res.meta.msg,
                        type: 'success'
                        });   
                }else{
                    //
                    this.$message({
                        message: res.meta.msg,
                        type: 'warning'
                        });
                }
                 this.getroleslist()
            },
            //
            async fenpeiqx(v){
                //id
                this.fpid = v.id
               let { data:res }= await this.$http.get('rights/tree')
               if(res.meta.status !== 200){
                   return this.$message.error("")
               }
                this.list1 = res.data
                console.log(this.list1)
                this.getkeys(v,this.defkeys)
                this.dialogVisible2 = true
            },
            //
            getkeys(node,arr){
                if(!node.children){
                    return arr.push(node.id)
                }
                node.children.forEach(item =>this.getkeys(item,arr))
            },
            //
            gbfp(){
                this.defkeys = []
            },
            //
           async qdfptk(){
               let keys = [
                   ...this.$refs.treeref.getCheckedKeys(),
                   ...this.$refs.treeref.getHalfCheckedNodes()
               ]
               let keys2 = keys.join(',')
               let { data:res } = await this.$http.post(`roles/${this.fpid}/rights`,{rids:keys2})
                console.log(res)
                if(res.meta.status == 200){
                    //  
                    this.$message({
                        message: res.meta.msg,
                        type: 'success'
                        });   
                }else{
                    //
                    this.$message({
                        message: res.meta.msg,
                        type: 'warning'
                        });
                }
                this.dialogVisible2 = false
                this.getroleslist()
            }

   },
   // - this
   created() {
       this.getroleslist()
   },
   // - DOM
   mounted() {

   },
   beforeCreate() {}, // - 
   beforeMount() {}, // - 
   beforeUpdate() {}, // - 
   updated() {}, // - 
   beforeDestroy() {}, // - 
   destroyed() {}, // - 
   activated() {}, //keep-alive
}
</script>
<style  lang='less' scoped>
.el-card{
    width: 100%;
    margin-top: 20px;
}
.el-table{
    margin-top: 20px;
}
.el-tag{
    margin: 20px;
}
.bdbottom{
    border-bottom: 1px solid #eee;
}
.bdtop{
    border-top: 1px solid #eee;
}
.el-col1{
 display: flex;
 align-items: center;
}
</style>