import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from'../components/Login.vue'
import Sy from'../components/Sy.vue'
import Wekcome from '../components/Wekcome.vue'
import Users from '../components/page/Users.vue'//
import Roles from '../components/page/Roles.vue'//
import Rights from '../components/page/Rights.vue'//
import Goods from '../components/page/Goods.vue'//
import Params from '../components/page/Params.vue'//
import Categories from '../components/page/Categories.vue'//
import Orders from '../components/page/Orders.vue'//
import Reports from '../components/page/Reports.vue'//
import Add from '../components/page/Add.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect:"/login"
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  //
  {
    path: '/login',
    name: 'Login',
    component: () => import('../components/Login.vue')
  },
  //
  {
    path: '/sy',
    name: 'Sy',
    component: () => import('../components/Sy.vue'),
    redirect:"/wekcome",
    children:[
      {
        path:"/wekcome",
        component: () => import('../components/Wekcome.vue'),
      },
      //
      {
        path:"/users",
        component: () => import('../components/page/Users.vue'),
      },
      //
      {
        path:"/roles",
        component: () => import('../components/page/Roles.vue'),
      },
      //
      {
        path:"/rights",
        component: () => import('../components/page/Rights.vue'),
      },
      //
      {
        path:"/goods",
        component: () => import('../components/page/Goods.vue'),
      },
      //
      {
        path:"/params",
        component: () => import('../components/page/Params.vue'),
      },
      //
      {
        path:"/categories",
        component: () => import('../components/page/Categories.vue'),
      },
      //
      {
        path:"/Orders",
        component: () => import('../components/page/Orders.vue'),
      },
      //
      {
        path:"/reports",
        component: () => import('../components/page/Reports.vue'),
      },
      {
        path:"/goods/add",
        component: () => import('../components/page/Add.vue'),
      },
    ]
  }
]

const router = new VueRouter({
  routes
})

export default router
