<template>
   <div class='users'>
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/sy' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <!--  -->
            <el-row :gutter="20">
                <el-col :span="10">
                    <el-input  clearable @clear='getuserList' v-model="queryInfo.query" placeholder="" class="input-with-select">
                        <el-button @click="getuserList" slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="6">
                     <el-button type="primary"  @click="dialogVisible =true"></el-button>
                </el-col>
            </el-row>
            <!--  -->
             <el-table
                :data="list"
                border stripe
                style="width: 100%">
                <el-table-column
                    label="#"
                   type ="index">
                </el-table-column>
                <el-table-column
                    prop="username"
                    label=""
                    width="180">
                </el-table-column>
                 <el-table-column
                    prop="email"
                    label=""
                    width="180">
                </el-table-column>
                 <el-table-column
                    prop="mobile"
                    label=""
                    width="180">
                </el-table-column>
                <el-table-column
                    prop="role_name"
                    label="">
                </el-table-column>
                <el-table-column label=""  width="80">
                    <template slot-scope="scope">
                        <el-switch @change="xgzt(scope.row)" v-model="scope.row.mg_state"></el-switch>
                    </template>
                </el-table-column>
                <el-table-column label=""  width="220">
                    <template slot-scope="scope">
                            <!--  -->
                             <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                            <el-button @click="xiugai(scope.row)" type="primary" size="mini" icon="el-icon-edit" ></el-button>
                            </el-tooltip>
                            <!--  -->
                             <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                            <el-button @click="shanchu(scope.row)" type="danger" size="mini" icon="el-icon-delete" ></el-button>
                            </el-tooltip>
                            <!--  -->
                            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
                                <el-button  @click="fpjs(scope.row)" type="warning" size="mini" icon="el-icon-setting"></el-button>
                            </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
             <!--  -->
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[1, 2, 5, 10]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
            </el-pagination>
        </el-card>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="dialogVisible"
            @close="adddialog"
            width="60%">
            <!--  -->
            <el-form :model="addFrom" :rules="addRules"  ref="addref" label-width="70px">
                <el-form-item  label="" prop="username">
                    <el-input v-model="addFrom.username"></el-input>
                </el-form-item>
                <el-form-item  label="" prop="password">
                    <el-input type="password" v-model="addFrom.password"></el-input>
                </el-form-item>
                <el-form-item  label="" prop="email">
                    <el-input v-model="addFrom.email"></el-input>
                </el-form-item>
                <el-form-item  label="" prop="mobile">
                    <el-input v-model="addFrom.mobile"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible=false"> </el-button>
                <el-button type="primary" @click="addqd"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="dialogVisible1"
            @close="xiugaidialog"
            width="60%">
            <!--  -->
            <el-form :model="xiugaiFrom" :rules="addRules"  ref="xiugairef" label-width="70px">
                <el-form-item  label="" prop="username">
                    <el-input v-model="xiugaiFrom.username"  :disabled="true"></el-input>
                </el-form-item>
                <el-form-item  label="" prop="email">
                    <el-input v-model="xiugaiFrom.email"></el-input>
                </el-form-item>
                <el-form-item  label="" prop="mobile">
                    <el-input v-model="xiugaiFrom.mobile"></el-input>
                </el-form-item>
            </el-form>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible1=false"> </el-button>
                <el-button type="primary" @click="xiugaiqd"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="dialogVisible2"
             @close="xjs"
            width="60%">
            <!--  -->
            <p>{{ fplist.username }}</p>
            <p>{{ fplist.role_name }}</p>
            <p>
                
                <el-select v-model="fenpei" placeholder="">
                    <el-option
                    v-for="item in fenplist"
                    :key="item.id"
                    :label="item.roleName"
                    :value="item.id">
                    </el-option>
                </el-select>
            </p>
            <!--  -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible2=false"> </el-button>
                <el-button type="primary" @click="qdfp"> </el-button>
            </span>
        </el-dialog>
   </div>
</template>

<script>
export default {
   data() {
   //
   //
 var  checkEmail = (rule,value,cb)=>{
        let yx = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
        if(yx.test(value)){

            return cb()
        }
        cb(new Error(''))
    }
    //
    var checkMobile =(rule,value,cb)=>{
         let sj =/^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
        if(sj.test(value)){

            return cb()
        }
        cb(new Error(''))
    }       
       return {
           queryInfo:{
               query:"",
               //
               pagenum:1,
               //
               pagesize:2
           },
           list:[],
            total:0,
            //
             dialogVisible:false,
             //
             dialogVisible1:false,
             //
               dialogVisible2:false,
               //
               fplist:{

               },
               //
               fenplist:[],
               //
               fenpei:"",
             //
             addFrom:{
                 username:"",
                 password:"",
                 mobile:"",
                 email:""   
             },
             //
              xiugaiFrom:{
                 username:"",
                 mobile:"",
                 email:"" ,
                 id:""  
             },
             //
             addRules:{
                 //
                  username: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 1, max: 5, message: ' 1  5 ', trigger: 'blur' }
                    ],
                    //
                    password:[
                        { required: true, message: '', trigger: 'blur' },
                        { min: 6, max: 14, message: ' 6  14 ', trigger: 'blur' }
                    ],
                    //
                     mobile:[
                         { required: true, trigger: 'blur' },
                        {  validator: checkMobile, trigger: 'blur' }
                     ],
                    //
                    email:[
                          { required: true,  trigger: 'blur' },
                             { validator: checkEmail, trigger: 'blur' }
                    ]
             },
               
       };
   },
   //import
   components: {

   },
   // data
   computed: {

   },
   //data
   watch: {

   },
   //
   methods: {
       //
       //
      async getuserList(){
          let { data:res } = await this.$http.get(`users`,{params:this.queryInfo})
          console.log(res)
          if(res.meta.status == 200){
              this.list = res.data.users
              this.total = res.data.total
              console.log(this.list)
          }else{
              this.$message.error("")
          }
       },
       //
       //pagesize
       handleSizeChange(newsize){
        //    console.log(newsize)
           //
           this.queryInfo.pagesize = newsize
           this.getuserList()
       },
       // 
       handleCurrentChange(newpage){
        //    console.log(newpage)
        //    
           this.queryInfo.pagenum = newpage
           this.getuserList()
       },
       //
      async xgzt(v){
        //    console.log(v)
          let {data:res} = await this.$http.put(`users/${v.id}/state/${v.mg_state}`)
            // console.log(res)
            if(res.meta.status == 200){
               this.$message({
                message: res.meta.msg,
                type: 'success'
                });
            }else{
                this.$message({
                message:"",
                type: 'warning'
                });
                v.mg_state = !v.mg_state
            }
       },
       //
       adddialog(){
           this.$refs.addref.resetFields()
       },
       //
       xiugaidialog(){
           this.$refs.xiugairef.resetFields()
       },
       //
        xjs(){
        this.fenpei = ""
       },

    //    
        addqd(){
            this.$refs.addref.validate(async valid =>{
                // console.log(valid)
                if (!valid) return
                //
              let { data:res } = await this.$http.post(`users`,this.addFrom)
                    // console.log(res) 
                    if(res.meta.status == 201){
                        //  
                        this.$message({
                            message: res.meta.msg,
                            type: 'success'
                            });   
                    }else{
                        //
                        this.$message({
                            message: res.meta.msg,
                            type: 'warning'
                            });
                    }
                    this.dialogVisible = false
            })
            
        },
        //
      async xiugai(v){
           console.log(v)
        let {data:res} = await this.$http.get(`users/${ v.id }`)
            // console.log(res)
            if(res.meta.status !== 200){
             return this.$message({
                    message: res.meta.msg,
                    type: 'warning'
                    });
            }
            this.xiugaiFrom.username = res.data.username
            this.xiugaiFrom.mobile = res.data.mobile
            this.xiugaiFrom.email =res.data.email
             this.xiugaiFrom.id =res.data.id
            this.dialogVisible1 = true
        },
        //
        xiugaiqd(){
            this.$refs.xiugairef.validate(async valid =>{
                // console.log(valid)
                if (!valid) return
                //
              let { data:res } = await this.$http.put(`users/${this.xiugaiFrom.id}`,this.xiugaiFrom)
                    // console.log(res) 
                    if(res.meta.status == 200){
                        //  
                        this.$message({
                            message: res.meta.msg,
                            type: 'success'
                            });   
                    }else{
                        //
                        this.$message({
                            message: res.meta.msg,
                            type: 'warning'
                            });
                    }
                    this.dialogVisible1 = false
                    //
                    this.getuserList()
            })
        },
        //
       async shanchu(v) {
            console.log(v.id)
        let conshanchu= await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err =>{
            return err
        })
        //confirm
        if(conshanchu !== "confirm"){
            return this.$message.info("")
        }
        //
        let { data:res } = await this.$http.delete('users/'+v.id)
        console.log(res)
        if(res.meta.status == 200){
            //  
            this.$message({
                message: res.meta.msg,
                type: 'success'
                });   
        }else{
            //
            this.$message({
                message: res.meta.msg,
                type: 'warning'
                });
        }
        //
        this.getuserList()
      },
      //
     async fpjs(v){
          this.fplist = v
         //
         let { data:res } = await this.$http.get(`roles`)
         
         if(res.meta.status !== 200){
            return this.$message.error("")
        }
        this.fenplist = res.data
        console.log(res)
          this.dialogVisible2=true
      },
      //
      async qdfp(){
            if(!this.fenpei){
            return this.$message.error("")
            }
        let { data:res} = await this.$http.put(`users/${this.fplist.id}/role`,{
            rid:this.fenpei
        })
        if(res.meta.status == 200){
            //  
            this.$message({
                message: "",
                type: 'success'
                });   
        }else{
            //
            this.$message({
                message: res.meta.msg,
                type: 'warning'
                });
        }
        //
        this.getuserList()
        this.dialogVisible2=false
      }


   },
   // - this
   created() {
       this.getuserList()
   },
   // - DOM
   mounted() {

   },
   beforeCreate() {}, // - 
   beforeMount() {}, // - 
   beforeUpdate() {}, // - 
   updated() {}, // - 
   beforeDestroy() {}, // - 
   destroyed() {}, // - 
   activated() {}, //keep-alive
}
</script>
<style lang="less" scoped>
.el-card{
    width: 100%;
    margin-top: 20px;
}
.el-table{
    margin-top: 20px;
}
.el-pagination{
    margin-top: 20px;
}
</style>