<template>
   <div class='sy'>
       <el-container>
        <!--  -->
        <el-header>
            <p>
                 <img src="../assets/logo.png" alt=""> 
                 
            </p>
            <el-button type="info" @click="tuichu"></el-button>
        </el-header>
        <el-container>
            <!--  -->
            <el-aside :width="iscollapse ? '64px' :'200px'">
                <div class="toggle" @click="toggle">|||</div>
                 <el-menu
                 :unique-opened="true"
                 :collapse="iscollapse"
                 :collapse-transition="false"
                 :router="true"
                 :default-active="luyou"
                 background-color="#333744"
                 text-color="#fff"
                 active-text-color="#409bff"
                class="el-menu-vertical-demo"
                @open="handleOpen"
                @close="handleClose">
                <el-submenu :index="item.id +''" v-for="(item) in lblist" :key="item.id">
                    <template slot="title">
                   <i :class="iobj[item.id]"></i>
                    <span>{{ item.authName }}</span>
                    </template>
                    <el-menu-item-group >
                    <el-menu-item :index="'/'+c.path +''" v-for="(c,index) in item.children" :key="index" @click="dianji('/'+c.path)">
                        <i class="el-icon-menu"></i>
                        {{ c.authName }}
                    </el-menu-item>
                    </el-menu-item-group>
                </el-submenu>
                </el-menu>
            </el-aside>
            <!--  -->
            <el-main>
              <!--  -->
              <router-view></router-view>
            </el-main>
        </el-container>
        </el-container>
   </div>
</template>

<script>
export default {
   data() {
   //
       return {
           lblist:[],//
           iobj:{
               '125':"el-icon-s-custom",
               '103':"el-icon-copy-document",
               '101':"el-icon-shopping-bag-2",
               '102':"el-icon-notebook-1",
               '145':"el-icon-data-line"
           },
            iscollapse:false,
            luyou:"",//
       };
   },
   //import
   components: {

   },
   // data
   computed: {

   },
   //data
   watch: {

   },
   //
   methods: {
       tuichu(){
           //Token
           window.sessionStorage.clear()
           //
           this.$router.push("/login")
           this.$message({
                message: "",
                type: 'success'
                });
       },
        handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
        //
      async getMenuList(){
        let { data:res }= await this.$http.get(`menus`)
        if(res.meta.status == 200){
             this.lblist = res.data
        }
         console.log(this.lblist)         
  
      },
      //
        toggle(){
            this.iscollapse = !this.iscollapse
        },
        //
        dianji(v){
            console.log(v)
            window.sessionStorage.setItem("luyou",v)
            this.luyou = v
        }
   },
   // - this
   created() {
       this.getMenuList()
       this.luyou = window.sessionStorage.getItem("luyou")
   },
   // - DOM
   mounted() {

   },
   beforeCreate() {}, // - 
   beforeMount() {}, // - 
   beforeUpdate() {}, // - 
   updated() {}, // - 
   beforeDestroy() {}, // - 
   destroyed() {}, // - 
   activated() {}, //keep-alive
}
</script>
<style lang="less" scoped>
.sy{
    height: 100%;
}
//
.el-container{
   height: 100%;
}
//
.el-header{
    background: #373D41;
    display: flex;
    justify-content: space-between;
    align-items: center;
    p{
        height: 100%;
        font-size: 24px;
        color: #fff;
        display: flex;
        align-items: center;
        img{
           height: 100%;
        }
        
    }
}
//
.el-aside{
    background: #333744;
    .el-menu{
        border-right:none;
    }
}
//
.el-main{
    background: #eaedf1;
}
//
.toggle{
    background: #4a5064;
    color: #fff;
    text-align: center;
    line-height: 24px;
    font-size: 10px;
    cursor: pointer;
    letter-spacing: 0.2em;
}
</style>