<template>
   <div class='rights'>
      <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/sy' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card> 
             <el-table
                :data="list"
                border stripe
                style="width: 100%">
                <el-table-column
                  label="#"
                   type ="index">
                </el-table-column>
                <el-table-column
                    prop="authName"
                    label=""
                    >
                </el-table-column>
                 <el-table-column
                    prop="path"
                    label=""
                  >
                </el-table-column>
                <el-table-column
                    label=""
                    >
                    <template slot-scope="scope">
                        <el-tag v-if="scope.row.level == 0">{{ scope.row.level | gl}}</el-tag>
                        <el-tag  v-if="scope.row.level == 1" type="success">{{ scope.row.level | gl}}</el-tag>
                        <el-tag  v-if="scope.row.level == 2" type="warning">{{ scope.row.level | gl}}</el-tag>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
   </div>
</template>

<script>
export default {
   data() {
   //
       return {
          list:[]
       };
   },
   filters:{
      gl(val){
         if(val == "0"){
            return ""
         }else if(val == "1"){
             return ""
         }else if(val == "2"){
             return ""
         }
      }
   },
   //import
   components: {

   },
   // data
   computed: {

   },
   //data
   watch: {

   },
   //
   methods: {
      async getrightslist(){
           let { data:res } = await this.$http.get(`rights/list`)
           this.list = res.data
           console.log(this.list)
       }
   },
   // - this
   created() {
       this.getrightslist()
   },
   // - DOM
   mounted() {

   },
   beforeCreate() {}, // - 
   beforeMount() {}, // - 
   beforeUpdate() {}, // - 
   updated() {}, // - 
   beforeDestroy() {}, // - 
   destroyed() {}, // - 
   activated() {}, //keep-alive
}
</script>
<style lang="less" scoped>
.el-card{
    width: 100%;
    margin-top: 20px;
}
.el-table{
    margin-top: 20px;
}

</style>