<template>
   <div class='goods'>
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/sy' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <!--  -->
            <el-row :gutter="20">
                <el-col :span="10">
                    <el-input clearable @clear='getfllist'  v-model="queryInfo.query" clearable placeholder="" class="input-with-select">
                        <el-button @click="getfllist" slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="6">
                     <el-button type="primary" @click="tz"></el-button>
                </el-col>
            </el-row>
            <!--  -->
             <el-table
                :data="list"
                border stripe
                style="width: 100%">
                <el-table-column
                    label="#"
                   type ="index">
                </el-table-column>
                <el-table-column
                    prop="goods_name"
                    label=""
                    width="520">
                </el-table-column>
                 <el-table-column
                    prop="goods_price"
                    label="">
                </el-table-column>
                 <el-table-column
                    prop="goods_weight"
                    label="">
                </el-table-column>
                <el-table-column  
                    label=""
                     width="160">
                     <template slot-scope="scope">
                            {{ scope.row.add_time | shijian}}
                     </template>
                </el-table-column>
                <el-table-column label=""  width="160">
                    <template slot-scope="scope">
                             <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                            <el-button type="primary" size="mini" icon="el-icon-edit" ></el-button>
                            </el-tooltip>
                             <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
                            <el-button @click="shanchu(scope.row)" type="danger" size="mini" icon="el-icon-delete" ></el-button>
                            </el-tooltip>
                    </template> 
                </el-table-column>
            </el-table>
             <!--  -->
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[1, 5, 10, 20]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
            </el-pagination>
        </el-card>
   </div>
</template>

<script>
export default {
   data() {
   //
       return {
           list:[],
           queryInfo:{
               query:"",
               //
               pagenum:1,
               //
               pagesize:10
           },
            total:0,

       };
   },
   //import
   components: {

   },
   filters:{
       shijian(time){
           let date   = new Date(time)//
        let year   = date.getFullYear();
        let month  = date.getMonth()+1;
        let day    = date.getDate();
        let hour   = date.getHours(); 
        let minute = date.getMinutes(); 
        let second = date.getSeconds(); 
        return year+"-"+month+"-"+day+" "+hour+":"+minute+":"+second;
       }
   },
   // data
   computed: {

   },
   //data
   watch: {

   },
   //
   methods: {
       //
       tz(){
           this.$router.push('/goods/add')
       },
    //    
      async getfllist(){
          let { data:res } = await this.$http.get(`goods`,{params:this.queryInfo})
           if(res.meta.status !== 200){
           return this.$message.error("")
       }
          this.list = res.data.goods
            this.total = res.data.total
          console.log(res)
       },
       //
       //pagesize
       handleSizeChange(newsize){
           console.log(newsize)
           //
           this.queryInfo.pagesize = newsize
         this.getfllist()
       },
       // 
       handleCurrentChange(newpage){
           console.log(newpage)
        //    
           this.queryInfo.pagenum = newpage
           this.getfllist()
       },
        //
       async shanchu(v) {
            // console.log(v.goods_id)
        let conshanchu= await this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).catch(err =>{
            return err
        })
        //confirm
        if(conshanchu !== "confirm"){
            return this.$message.info("")
        }
        //
        let { data:res } = await this.$http.delete('goods/'+v.goods_id)
        console.log(res)
        if(res.meta.status == 200){
            //  
            this.$message({
                message: res.meta.msg,
                type: 'success'
                });   
        }else{
            //
            this.$message({
                message: res.meta.msg,
                type: 'warning'
                });
        }
        //
        this.getfllist()
      },
   },
   // - this
   created() {
       this.getfllist()
   },
   // - DOM
   mounted() {

   },
   beforeCreate() {}, // - 
   beforeMount() {}, // - 
   beforeUpdate() {}, // - 
   updated() {}, // - 
   beforeDestroy() {}, // - 
   destroyed() {}, // - 
   activated() {}, //keep-alive
}
</script>
<style lang="less" scoped>
.el-card{
    width: 100%;
    margin-top: 20px;
}
.el-table{
    margin-top: 20px;
}
.el-pagination{
    margin-top: 20px;
}

</style>